body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    color: #333;
  }
  
  h1, h2 {
    text-align: center;
    color: #444;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .product-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
  }
  
  .product-item {
    background-color: #fff;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    flex: 1 1 30%;
    max-width: 30%;
    transition: transform 0.3s ease;
  }
  
  .product-item:hover {
    transform: translateY(-10px);
  }
  
  .product-info h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #007BFF;
  }
  
  .product-info p {
    font-size: 1.2rem;
    color: #555;
  }
  
  .status {
    text-align: center;
    font-weight: bold;
    margin-top: 20px;
    padding: 10px;
    background-color: #007BFF;
    color: #fff;
    border-radius: 8px;
  }
  